$square-width:100px;
$padding: 10px;
$gap:1px;
$calendar-width: $square-width * 7 + $gap * 6 + $padding * 2;
$month-length: 29;
$start-point: $square-width + $gap;

 $week:
   "mon" 0,
   "tue" $start-point,
   "wen" $start-point * 2,
   "thu" $start-point * 3,
   "fri" $start-point * 4,
   "sat" $start-point * 5,
   "sun" $start-point * 6;
